@import "../../styles/variables";

.searchPage {
	@include pageDefault;
	&-container {
		position: relative;
		z-index: 2;
		margin: 0 auto;
		max-width: 1400px;
		height: 100%;
		width: 100%;
		&__content {
			display: flex;
			justify-content: space-between;
			gap: 24px;
			margin-bottom: 64px;
		}
		margin-top: 64px;
	}
}
