@import "../../styles/variables";

.anime-page {
  &__info {
    display: flex;
  }
}
.anime-info {
  &__leftside {
    padding-right: 12px;
  }
  &__image {
    img {
      width: clamp(200px, 300px, 400px);
      object-fit: cover;
    }
  }
  &__rate {
    cursor: pointer;
    margin-top: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: clamp(200px, 100%, 300px);
    height: 42px;
    padding-left: 16px;
    background-color: $blockActiveColor;
    border-radius: 8px;
    transition: all 0.2s ease-in-out;
    &:hover {
      background-color: $buttonColorPrimary;
    }
    img {
      border-right: 1px solid $halfopacity;
      padding-right: 12px;
    }
    P {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
    }
  }
  &__addlist {
    transition: all 0.2s ease-in-out;
    &:hover {
      background-color: $buttonColorPrimary;
    }
    cursor: pointer;
    margin-top: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: clamp(200px, 100%, 300px);
    height: 42px;
    padding: 8px;
    background-color: $blockActiveColor;
    border-radius: 8px;
    span {
      margin-right: 4px;
      font-weight: 700;
    }
    p {
      margin-right: 4px;
      font-size: 16px;
    }
    img {
      margin-top: 2px;
      height: 4px;
    }
  }
  &__rightside {
    padding-left: 12px;
  }
  &-title {
    margin-top: 12px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    p {
      border-right: 1px solid $halfopacity;
      margin-right: 12px;
      padding-right: 12px;
      font-size: 24px;
      font-weight: 700;
    }
    span {
      color: $gray;
    }
  }
}

@media (max-width: 800px) {
  .anime-page__container {
    padding: 12px;
  }
  .anime-info-title {
    justify-content: center;
    align-items: center;
  }
  .anime-page__info {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &-title {
      justify-content: center;
      align-items: center;
    }
  }
  .anime-info__image {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: clamp(200px, 100%, 300px);
    }
  }
  .anime-info__leftside {
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .anime-info__rightside {
    padding: 0px;
  }
}
